@keyframes scroll {
  0% {
    transform: translateY(40%);
  }

  100% {
    transform: translateY(-100%);
  }
}

.scrolling-lines {
  animation: scroll 20s linear infinite;
  /* Adjust the duration as needed */
}

.blogs {
  text-decoration: none;
}

.blogs:hover {
  text-decoration: underline;
  color: blue;
}